<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3></h3>
        </div>
        <Header />
        <div class="tableStyle1 trackTable">
          <table>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th width="25%"></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <template v-for="c in ClassRooms">
              <tr v-for="(l, i) in c.LessonList" :key="l.LessonID">
                <template v-if="i == 0">
                  <td :rowspan="c.Lessons.length">
                    {{ c.CreateTime | dateFormat }}
                  </td>
                  <td :rowspan="c.Lessons.length">
                    {{ c.ClassRoomName }}
                  </td>
                  <td :rowspan="c.Lessons.length">{{ c.LessonTitle }}</td>
                  <td>
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                  <td>{{ l.FinishLesson | commaFormat }}</td>
                  <td>{{ l.FinishHomeWork | commaFormat }}</td>
                  <td>
                    {{c.StudentCount | commaFormat}}
                    <a
                      class="studentDetail"
                      href="javascript:void(0)"
                      @click="show(l, c)"
                      ><img src="/img/zoom.svg"
                    /></a>
                  </td>
                </template>
                <template v-else>
                  <td>
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                  <td>{{ l.FinishLesson | commaFormat }}</td>
                  <td>{{ l.FinishHomeWork | commaFormat }}</td>
                  <td>
                    {{c.StudentCount | commaFormat}}
                    <a
                      class="studentDetail"
                      href="javascript:void(0)"
                      @click="show(l, c)"
                      ><img src="/img/zoom.svg"
                    /></a>
                  </td>
                </template>
              </tr>
            </template>
          </table>
        </div>
      </div>
    </div>
    <div class="popUp" v-if="ShowPopup && SelectedLesson">
      <div class="alertWindow">
        <div class="p_head">
          <p></p>
        </div>
        <div class="p_content">
          <h3>{{SelectedClassRoom.ClassRoomName}}</h3>
          <div class="archiveTable tableStyle2">
            <div class="scroller">
              <table id="export-table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(d, i) in LessonDetail" :key="i">
                    <td>{{ d.Name }}</td>
                    <td>{{ d.Progress }}%</td>
                    <template v-if="d.CorrectCount != null">
                      <template v-if="d.FinishTime">
                        <td></td>
                        <td>{{ d.CorrectCount }}/{{ d.TotalCount }}</td>
                      </template>
                      <template v-else>
                        <td colspan="2"></td>
                      </template>
                    </template>
                    <td colspan="2" v-else></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="p_foot">
          <div class="downloadExcel">
            <a href="javascript:void(0)" @click="excel">Excel</a>
          </div>
          <div class="confirmBtn">
            <a href="javascript:void(0)" @click="ShowPopup = false"></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Teacher/Header.vue";
import XLSX from "xlsx";

export default {
  components: {
    Header,
  },
  data() {
    return {
      ClassRooms: [],
      ShowPopup: false,
      SelectedLesson: null,
      SelectedClassRoom: null,
      LessonDetail: [],
    };
  },
  async mounted() {
    this.setBase("tSpace page");
    var response = await this.$api.getClassRoomTrace();
    var map = {};
    response.Lessons.forEach((l) => {
      map[l.LessonID] = l;
    });
    response.ClassRooms.forEach((c) => {
      var list = [];
      c.Lessons.forEach((l) => list.push(map[l]));
      c.LessonList = list;
      list.forEach((l) => {
        var key = `${c.ClassRoomID}-${l.LessonID}`;
        l.FinishHomeWork = response.FinishHomeWork[key] || 0;
        l.FinishLesson = response.FinishLesson[key] || 0;
      });
      c.StudentCount = response.ClassRoomMemberCount[c.ClassRoomID] || 0;
    });
    this.ClassRooms = response.ClassRooms;
  },
  methods: {
    async show(lesson, classroom) {
      this.SelectedLesson = lesson;
      this.SelectedClassRoom = classroom;
      var response = await this.$api.getLessonSummery(lesson.LessonID, classroom.ClassRoomID);
      this.LessonDetail = response;
      this.ShowPopup = true;
    },
    excel() {
      var wb = XLSX.utils.book_new();
      var ws = XLSX.utils.table_to_sheet(
        document.getElementById("export-table")
      );
      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
      XLSX.writeFile(wb, this.SelectedClassRoom.ClassRoomName + ".xlsx");
    },
  },
};
</script>